
import Loading from 'vux/src/components/loading/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'

import { mapGetters } from 'vuex'

export default {
name: 'app',
components: {
Loading,
Toast,
Alert,
Confirm
},
computed: {
...mapGetters({
loadingXp: 'loading',
toastXp: 'toast',
alertXp: 'alert',
confirmXp: 'confirm'
}),
toastXpShow: {
get () {
return this.toastXp.show
},
set (show) {
this.$store.commit('toast', {show})
}
},
alertXpShow: {
get () {
return this.alertXp.show
},
set (show) {
this.$store.commit('alert', {show})
}
},
confirmXpShow: {
get () {
return this.confirmXp.show
},
set (show) {
this.$store.commit('confirm', {show})
}
}
}
}



